.footer {
    width: 100%;
    background-color: var(--darkgrey);
   
}

.footer .container {
    max-width: 9240px;
    padding: 3rem 1rem;
    margin: auto;
    overflow: hidden;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 2fr;
}

.footer h6,
p {
    color: var(--grey);
    font-size: 14px;
    padding: .4rem 0;
}

.footer .link {
    color: var(--lightgrey);
    font-size: 14px;
    padding: .4rem 0;
    display: block;
}

.footer .subscribe {
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer input {
    padding: .4rem;
    font-size: 14px;
    border-radius: 4px;
    width: 250px;
    border: 1px solid var(--lightgrey);
}

.footer button {
    margin-left: .8rem;
    width: 150px;
}

.footer .content {
    max-width: 1240px;
    margin: auto;
    padding: 2rem;
    display: flex;
    justify-content: space-between;
    border-top: 1px solid var(--grey)
}


@media screen and (max-width:960px) {
    .footer .container {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        padding: 1rem;
    }

    .footer .solutions,
    .support {
        display: none;
    }

    .footer .col-subscribe {
        margin: 2rem;
        text-align: center;
    }

    .footer .subscribe {
        flex-direction: column;
    }

    .footer input {
        width: 100%;
    }

    .footer button {
        width: 100%;
        margin: .2rem 0;
    }

    .footer-bottom .content {
        padding: 1rem;
        justify-content: center;
    }

    .footer-bottom .right {
        display: none;
    }
}
.pagination {
    display: inline-block;
    padding-left: 10;
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-radius: 0.25rem;
    margin-left: 30px;
}

.page-item {
    display: inline;
}

.page-item:first-child .page-link {
    margin-left: 0;
    border-bottom-left-radius: 0.25rem;
    border-top-left-radius: 0.25rem;
}

.page-item:last-child .page-link {
    border-bottom-right-radius: 0.25rem;
    border-top-right-radius: 0.25rem;
}

.page-item.active .page-link,
.page-item.active .page-link:focus,
.page-item.active .page-link:hover {
    z-index: 2;
    color: #fff;
    cursor: default;
    background-color: green;
    border-color: #5cb85c;
}

.page-item.disabled .page-link,
.page-item.disabled .page-link:focus,
.page-item.disabled .page-link:hover {
    color: #818a91;
    pointer-events: none;
    cursor: not-allowed;
    background-color: #fff;
    border-color: #ddd;
}

.page-link {
    position: relative;
    float: left;
    padding: 0.5rem 0.75rem;
    margin-left: -1px;
    color: #5cb85c;
    text-decoration: none;
    background-color: #fff;
    border: 1px solid #ddd;
    cursor: pointer;
}

.page-link:focus,
.page-link:hover {
    color: #3d8b3d;
    background-color: #eceeef;
    border-color: #ddd;
}
